.app-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
  background-color: #2E1A47;
  color: #ffffff;
}

.content-container {
  flex: 1;
  overflow-y: auto;
  padding-bottom: 60px;
}

.main-content {
  padding: 20px;
}

.header-image {
  background: linear-gradient(135deg, #4B2E83, #F5C563);
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
}

.header-title {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

.button-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.sleep-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
}

.story-card {
  display: flex;
  align-items: center;
  background: #4B2E83;
  border-radius: 10px;
  margin-bottom: 15px;
  padding: 10px;
  cursor: pointer;
}

.story-cover {
  width: 60px;
  height: 60px;
  border-radius: 10px;
  margin-right: 15px;
}

.story-info {
  flex: 1;
}

.story-title {
  font-size: 18px;
  margin: 0;
}

.story-author {
  font-size: 14px;
  color: #E5989B;
  margin: 0;
}

.play-icon {
  background: none;
  border: none;
  color: #F5C563;
  font-size: 24px;
  cursor: pointer;
}

.pulse {
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.player-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.player-container {
  background: #2E1A47;
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
  text-align: center;
}

.player-cover {
  width: 100%;
  height: 200px;
  border-radius: 10px;
  margin-bottom: 20px;
}

.player-title {
  font-size: 20px;
  margin: 0;
}

.player-author {
  font-size: 16px;
  color: #E5989B;
  margin: 10px 0;
}

.player-controls {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 20px 0;
}

.player-button {
  background: none;
  border: none;
  cursor: pointer;
}

.play-button {
  background: #F5C563;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.time {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}

.progress-bar {
  position: relative;
  height: 5px;
  background: #4B2E83;
  border-radius: 5px;
  margin: 10px 0;
  cursor: pointer;
}

.progress {
  height: 100%;
  background: #F5C563;
  border-radius: 5px;
}

.thumb {
  position: absolute;
  top: -5px;
  width: 15px;
  height: 15px;
  background: #F5C563;
  border-radius: 50%;
  transform: translateX(-50%);
}

.action-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
  margin: 10px;
}

.top-left {
  position: absolute;
  top: 10px;
  left: 10px;
}

.mini-player {
  position: fixed;
  bottom: 60px;
  left: 0;
  right: 0;
  background: #4B2E83;
  display: flex;
  align-items: center;
  padding: 10px;
  z-index: 1000;
}

.mini-player-cover {
  width: 40px;
  height: 40px;
  border-radius: 5px;
  margin-right: 10px;
}

.mini-player-info {
  flex: 1;
}

.mini-player-title {
  font-size: 14px;
  margin: 0;
}

.mini-player-author {
  font-size: 12px;
  color: #E5989B;
  margin: 0;
}

.mini-player-controls {
  display: flex;
  gap: 10px;
}

.nav-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: #4B2E83;
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
}

.nav-button {
  background: none;
  border: none;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 12px;
  cursor: pointer;
}

.nav-button.active {
  color: #F5C563;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: #2E1A47;
  border-radius: 10px;
  padding: 20px;
  width: 90%;
  max-width: 400px;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-title {
  font-size: 20px;
  margin: 0;
}

.modal-close-button {
  background: #F5C563;
  color: #2E1A47;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.modal-body {
  font-size: 14px;
  line-height: 1.5;
}

.modal-body h3 {
  font-size: 16px;
  margin-top: 20px;
}

.modal-body ul {
  padding-left: 20px;
}

html, body, #root, .app-container {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
  overflow: hidden;
}